.tech-tags,.tech-tags>li {
    margin: 0;
    padding: 0
}

.tech-tags>li {
    display: block
}

.tech-tags li {
    display: inline-block;
    overflow: hidden
}

.tech-tags a {
    display: inline-block;
    background-color: rgb(50,60,88, .8);
    /*background-color: rgba(175,34,34,.8);
     */
    color: #FFF;
    padding: 4px 10px;
    font-size: 13px;
    text-transform: uppercase
}

.tech-tags a:hover {
    background-color: rgb(50,60,88);
}

.project-section {
    max-width: 1400px
}

.project-section .project-descriptions {
    overflow: hidden;
    margin: 1em 0
}

.project-section .project-descriptions .project-description {
    position: relative
}

.project-section .project-descriptions .project-description>p:first-child,.project-section .project-descriptions .project-description>ul:first-child {
    margin-top: 0
}

@media screen and (min-width: 768px) {
    .project-section .project-descriptions .project-description {
        width:50%;
        float: left
    }

    .project-section .project-descriptions .project-description:first-child {
        padding-right: 20px
    }

    .project-section .project-descriptions .project-description+.project-description {
        padding-left: 20px;
        border-left: 1px solid #ddd
    }
}

.project-section .project-descriptions .project-description ul:not(.tech-icons):not(.tech-tags) {
    padding-left: 20px
}

.project-section .technologies-title {
    margin-top: 2em
}

.project-section .project-images {
    margin-top: 2em;
    text-align: center
}

.project-section .project-images img {
    max-width: 100%;
    margin-right: 1em;
    margin-bottom: 1em
}

.row {
    overflow: hidden
}

.col {
    display: block;
    float: left
}

.col-4 {
    width: 33.33333%
}

@media screen and (max-width: 450px) {
    .col-4 {
        width:auto;
        float: none
    }

    .col-4+.col-4 {
        margin-top: 2em
    }
}

.feature {
    color: #323C58; 
    /*color: #BB1E1E;*/
    font-weight: 300;
    text-align: center;
    padding: 0 10px
}

.feature .feature-number {
    font-size: 48px;
    line-height: 70px
}

.feature .feature-number img {
    display: block;
    margin: 0 auto;
    height: 70px;
    width: 70px
}

.feature .feature-description {
    margin-top: 10px;
    font-size: 20px;
    line-height: 1.3
}

.post {
    margin-bottom: 30px
}

.post .post-title {
    font-weight: 400;
    font-size: 36px;
    line-height: 1;
    margin-bottom: 10px;
    color: #BB1E1E;
    text-transform: none
}

.post .post-meta {
    font-size: 15px;
    color: #555;
    overflow: hidden
}

.post .post-meta.post-meta-top {
    color: #999
}

.post .post-meta .post-meta-left {
    float: left
}

.post .post-meta .post-meta-right {
    float: right
}

.post .post-meta a {
    color: inherit
}

.post .post-date {
    font-style: italic
}

.post .comment-link {
    color: #BB1E1E
}

.timeline.talks-timeline .talk-title {
    margin-top: -.25em
}

.timeline.talks-timeline .talk-buttons .button {
    margin-top: 1em
}

.timeline.talks-timeline .talk-image img {
    max-width: 100%
}

body.cv {
    font-family: Roboto,Helvetica,sans-serif;
    color: #000;
    background-color: #c1c1c1;
    font-size: 14px;
    line-height: 1.5
}

@media print {
    body.cv {
        background-color: #fff
    }
}

.cv-page {
    width: 920px;
    padding: 80px 80px 60px;
    margin: 80px auto;
    background-color: #E9EBDB;
    box-shadow: 2px 2px 1px #606060
}

@media print {
    .cv-page {
        padding: 40px 80px;
        box-shadow: none
    }
}

@media screen and (max-width: 1023px) {
    .cv-page {
        width:auto;
        margin: 0 auto;
        padding: 20px
    }
}

.cv-header {
    display: -ms-flexbox;
    display: flex
}

@media screen and (max-width: 1023px) {
    .cv-header {
        display:block;
        text-align: center
    }
}

.cv-header-avatar {
    float: left
}

.cv-header-avatar img {
    border-radius: 2px;
    height: 150px
}

@media screen and (max-width: 1023px) {
    .cv-header-avatar {
        float:none;
        margin-bottom: 15px
    }

    .cv-header-avatar img {
        border-radius: 6px
    }
}

.cv-header-text {
    -ms-flex: 1;
    flex: 1;
    padding: 0 15px
}

.cv-header-name {
    display: block;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 30px;
    line-height: 1;
    margin-bottom: 5px;
    color: #BB1E1E
}

.cv-header-subname {
    display: block;
    text-transform: none;
    font-size: 24px;
    font-weight: 300;
    margin-bottom: 5px;
    color: #777
}

.cv-header-meta {
    overflow: hidden;
    font-size: 14px;
    color: #777;
    white-space: nowrap
}

@media screen and (max-width: 1023px) {
    .cv-header-meta {
        white-space:normal;
        margin-top: 5px
    }
}

.cv-header-meta-left {
    float: left;
    padding-right: 15px;
    text-align: right
}

@media screen and (max-width: 1023px) {
    .cv-header-meta-left {
        float:none;
        padding: 0;
        text-align: center
    }
}

.cv-header-meta-right {
    float: right;
    border-left: 1px solid #e0e0e0;
    padding-left: 15px;
    text-align: left
}

@media screen and (max-width: 1023px) {
    .cv-header-meta-right {
        float:none;
        padding: 0;
        text-align: center;
        border-left: none
    }
}

@media screen and (max-width: 1023px) {
    .cv-header-meta-row {
        display:inline
    }

    .cv-header-meta-row+.cv-header-meta-row:before {
        content: " / "
    }
}

.cv-section {
    overflow: hidden
}

.cv-section+.cv-section {
    margin-top: 1.5em
}

@media screen and (max-width: 767px) {
    .cv-section+.cv-section {
        margin-top:40px
    }
}

.cv-section-title {
    color: #BB1E1E;
    font-size: 24px;
    font-weight: 300;
    padding-left: 21.5%;
    margin-bottom: 15px;
    page-break-after: avoid
}

@media screen and (max-width: 767px) {
    .cv-section-title {
        padding-left:0
    }
}

.cv-section-content.cv-section-content--indented {
    padding-left: 21.5%
}

@media screen and (max-width: 767px) {
    .cv-section-content.cv-section-content--indented {
        padding-left:0
    }
}

.cv-section-content ol,.cv-section-content ul:not(.tech-tags) {
    padding-left: 20px
}

.cv-tech-tags a {
    background-color: #e0e0e0;
    color: inherit
}

.cv-tech-tags a:hover {
    text-decoration: none;
    background-color: #BB1E1E;
    color: #fff
}

.cv-timeline-row {
    overflow: hidden;
    page-break-inside: avoid
}

.cv-timeline-row+.cv-timeline-row {
    margin-top: 15px
}

@media screen and (max-width: 767px) {
    .cv-timeline-row+.cv-timeline-row {
        margin-top:30px
    }
}

.cv-timeline-side {
    float: left;
    width: 21.5%;
    text-align: right;
    padding-right: 15px;
    color: #777;
    font-size: 13px
}

@media screen and (max-width: 767px) {
    .cv-timeline-side {
        float:none;
        width: auto;
        text-align: center;
        margin-bottom: 10px
    }
}

.cv-timeline-body {
    float: left;
    width: 78.5%
}

@media screen and (max-width: 767px) {
    .cv-timeline-body {
        float:none;
        width: auto
    }
}

.cv-timeline-body p,.cv-timeline-body ul {
    margin-top: 5px;
    margin-bottom: 5px
}

.cv-timeline-company {
    text-transform: uppercase
}

@media screen and (min-width: 768px) {
    .cv-timeline-company {
        background-color:#eee;
        padding: 3px 7px;
        margin-bottom: 5px;
        border-radius: 2px
    }
}

@media screen and (max-width: 767px) {
    .cv-timeline-company {
        background-color:#eee;
        padding: 3px 7px;
        margin-bottom: 5px;
        border-radius: 2px;
        text-align: center
    }
}

@media print {
    .cv-timeline-company {
        background: 0 0;
        padding: 0;
        font-size: 14px;
        margin-bottom: 0
    }
}

.cv-timeline-position {
    font-weight: 400;
    font-size: 18px;
    color: #333;
    line-height: 1.3
}
